.block-team {
  width: 100%;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--primary-color), 1);
}
@media (min-width: 1024px) {
  .block-team {
    padding: 120px 0;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    left: 100%;
  }
  100% {
    opacity: 1;
    left: 50%;
  }
}
.block-team[data-visibility=visible] .cols img {
  animation: 2s ease 0s 1 slideInFromLeft;
}
.block-team[data-visibility=visible] .cols .block-title,
.block-team[data-visibility=visible] .cols .social,
.block-team[data-visibility=visible] .cols .info {
  animation: 2s ease 0s 1 fadeIn;
}
.block-team .block__inner {
  max-width: 1800px;
  width: 95%;
  margin: 0 auto;
  display: flex;
}
.block-team .cols {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 14rem;
  row-gap: 3rem;
}
.block-team .cols .col {
  min-width: 280px;
  display: flex;
  align-self: flex-start;
  justify-content: center;
  flex: 1 0 calc(75% - 10rem);
}
.block-team .cols .col:first-child {
  flex: 1 0 calc(25% - 4rem);
}
.block-team .cols .col:last-child {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.block-team .cols .col .block-title {
  font-family: var(--adore);
  font-size: 8.2rem;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .block-team .cols .col .block-title {
    font-size: 36px;
  }
}
.block-team .cols .col .heading,
.block-team .cols .col .heading a {
  font-family: var(--montserrat);
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 3rem;
  text-transform: uppercase;
  color: rgba(var(--text-color), 1);
  animation: 2s ease 0s 1 fadeIn;
}
.block-team .cols .col .team {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  gap: 6rem;
}
.block-team .cols .col .team .member {
  min-width: 280px;
  padding-bottom: 1rem;
  flex: 1 0 calc(50% - 3rem);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1.5rem;
  border-bottom: 1px solid rgba(var(--text-color), 1);
}
.block-team .cols .col .team .member a {
  display: flex;
}
.block-team .cols .col .team .member a:hover {
  color: rgb(0, 0, 0);
}
.block-team .cols .col .team .member .photo {
  width: 100%;
  height: 350px;
  position: relative;
  flex: 0 0 100%;
  display: flex;
}
@media (min-width: 1024px) {
  .block-team .cols .col .team .member .photo {
    height: 550px;
  }
}
.block-team .cols .col .team .member .photo img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  object-fit: cover;
}
.block-team .cols .col .team .member .social {
  margin-bottom: 1.5rem;
  flex: 0 0 30%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 1rem;
}
.block-team .cols .col .team .member .social li {
  width: 32px;
  height: 32px;
  display: flex;
}
.block-team .cols .col .team .member .social li a svg {
  width: 100%;
  height: 100%;
  color: rgba(var(--tertiary-color), 1);
  fill: rgba(var(--tertiary-color), 1);
  stroke: rgba(var(--tertiary-color), 1);
}
.block-team .cols .col .team .member .social li a:hover svg {
  color: rgb(0, 0, 0);
  fill: rgb(0, 0, 0);
  stroke: rgb(0, 0, 0);
}
.block-team .cols .col .team .member .info {
  min-width: 250px;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.block-team .cols .col .team .member .position {
  font-size: 1.6rem;
}